<template>
  <div class="exam">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <p class="title-box">批次名称：<span>{{$route.query.batch}}</span></p>
      <div class="btn-block">
        <div @click="allocation()" class="btn-main-deepen">自动分配考生</div>
        <el-tooltip content="自动根据学生现在就读学校所在区域就近分配考点，分配后可手动调整" placement="bottom" effect="dark">
          <i class="el-icon-warning"></i>
        </el-tooltip>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="20%">考点名称</th>
          <th width="12%">最多容纳考生数</th>
          <th width="11%">报考人数</th>
          <th width="11%">推荐考生数</th>
          <th width="11%">录取数<p class="f-small gray-shallow">不含自主报名</p></th>
          <th width="11%">录取率</th>
          <th width="14%">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr>
          <td>未分配考生</td>
          <td></td>
          <td>{{undistributed.applyStudentNum}}</td>
          <td></td>
          <td></td>
          <td></td>
          <td>
            <el-tooltip class="tooltip" content="自动根据学生现在就读学校所在区域就近分配考点，分配后可手动调整" placement="top" effect="dark">
              <i class="el-icon-warning"></i>
            </el-tooltip>
            <span @click="allocation()" class="oper">自动分配</span>

            <span @click="goToExam()" class="oper">手动调整</span>
          </td>
        </tr>
        <tr v-for="item in table.list">
          <td>{{item.name}}</td>
          <td>{{item.capacity}}</td>
          <td :class="{'error': item.capacity < item.countStudentNum}">{{item.countStudentNum}}</td>
          <td>{{item.recommendStudentNum || '--'}}</td>
          <td>{{item.enrollStudentNum || '--'}}</td>
          <td>{{item.enrollPercent}}</td>
          <td>
            <span @click="goToExam(item)" class="oper">查看考生</span>
            <span @click="goToVideo(item)" class="oper">监考视频</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
    </main-part>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "考试管理", path: "go(-1)"}, {name: "查看考点", path: ""}],
        searchData: [
          {type: 'input', placeholder: '考点名称'}],
        table: {
          url: '/centre/queryExamCentrePage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            batchId: parseInt(this.$route.query.id),
            keywords: ''
          }
        },
        // 未分配考生
        undistributed: ''
      }
    },
    methods: {
      // 前往监考视频
      goToVideo(item) {
        this.$router.push({
          path: '/recruit/exam/site/videos', query: {
            id: item.id,
            name: item.name
          }
        })
      },
      // 分配考生
      async allocation() {
        await this.$confirm('是否确认操作自动分配考生？', '温馨提示：自动根据学生现在就读学校所在区域就近分配考点，分配后可手动调整。', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/centre/autoDistributionCentreStudent.do',
          query: {
            batchId: this.$route.query.id
          }
        })
        this.$remind('分配成功')
        this.$search(this.table)
      },
      // 获取未分配的考试
      async getUndistributed() {
        this.undistributed = await this.$ajax({
          url: '/rbatch/getBatchResidueStudent.do',
          query: {
            batchId: this.$route.query.id
          }
        })
      },
      // 前往查看考生
      async goToExam(item) {
        this.$router.push({
          path: '/recruit/exam/examinee-exam',
          query: {
            batch: this.$route.query.batch,
            name: !!item ? item.name : null,
            id: !!item ? item.id : null,
            batchId: this.$route.query.id,
            type: this.$route.query.type,
            hand: !item
          }
        })
      }
    },
    mounted(){
      this.getUndistributed()
    },
    activated() {
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .exam
    .title-box
      position: absolute
      line-height: 30px
      top: 17px
    .btn-block
      i
        line-height: 30px
        vertical-align: top
        margin-left: 10px
    .tooltip
      position: absolute
      margin-left: -18px
      margin-top: 2px
    .error
      color: $error-c
</style>
